import React from 'react';
import Video from './Video'
import Footer from "./Footer"
import '../components_css/Clothing.css';
import {CLOTHING_IMG_URLS} from "../Constants"
import Header from "./Header"
import mockup1 from "../images/mockup1.png"
import mockup2 from "../images/mockup2.png"
import mockup3 from "../images/mockup3.png"
import mockup4 from "../images/mockup4.png"
import mockup5 from "../images/mockup5.png"
import mockup6 from "../images/mockup6.png"
import mockup7 from "../images/mockup7.png"
import mockup8 from "../images/mockup8.png"



class Clothing extends React.Component {
  constructor(props) {
     super(props);
  }

    render(){

    const images = CLOTHING_IMG_URLS.map(url => {
      return <a key = {url.link} href={url.link} target='_blank'><img key = {url.link} src = {url.link} alt = "" className='clothing-img'/></a>
    });
  
    const scroll = () => {
      var element = document.getElementById("clothes");
      element.scrollIntoView({behavior: "smooth", inline: "nearest"});
    }

    return(
      <div className='clothing_container'>
        <Header show_class={'show-on-full'}/>
        <Video/>
        <div className='clothing_title'>
          <div className='title_inner' id='title_line'>KICKBACK CLOTHING</div>
          <div id='scroll-button'><span onClick={scroll}>&#8659;</span></div>
          <div className='clothing_img_container' id='clothes'>
            <div className='by-sam'>by sam.</div>
            <a target='_blank'><img src = {mockup1} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup2} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup3} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup4} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup5} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup6} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup7} alt = "" className='clothing-img'/></a>
            <a target='_blank'><img src = {mockup8} alt = "" className='clothing-img'/></a>

            <div className='clothing-footer'>
              <Footer/>
             </div>
          </div>
        </div>

      </div>
    )};
}

export default Clothing;
